body
{
  font-family: 'Consolas', 'Courier New', Courier, monospace;
  font-size: 1em;
  font-weight: 500;

  background-color: #282c34;
  color: #f0f8ff;

  --orange: #fe935c;
  --blue: #84fcf9;

  --normal-text: 1.35em;
  --main-title: 3em;
  --sub-title: 1.6em;
}

@media print
{
  body
  {
    background: #282c34 !important;
    -webkit-print-color-adjust: exact;
  }
}

h1,h2,h3,h4,h5,p,form{margin: 0;padding: 0;}
a
{
  text-transform: none;
  color: inherit;
}

.App {
  padding: 50px;
  position: relative;

  max-width: 1000px;
  margin: 0 auto;
}

._blue {color: var(--blue);}
._orange {color: var(--orange);}

.cv{font-size: 5em;font-weight: bolder;}

p{font-size: var(--normal-text);}

._flex
{
  display: flex;
}
._flex._flex_col{flex-direction: row;}
._flex._flex_row{flex-direction: column;}

._flex._flex1{flex:1;}
._flex._flex2{flex:2;}


._spacer
{
  margin-top: 20px;
}
._spacer>h2 {font-size: 3em;}
._spacer>h3 {font-size: 1.5em; font-weight: 300;}

._spacer>h1
{
  font-size: 3em;
  font-weight: 500;
  color: var(--orange);
  padding-left: 0.3em;
  position: relative;
}
._spacer>h1::before
{
  position: absolute;
  bottom: .25em;
  left: 0;
  height: 0.2em;
  width: 0.2em;
  background-color: var(--blue);
  content: '';
  border-radius: 50%;
}
._spacer>._qrcode
{
  float: right;
  position: relative;
  height: 250px;
  width: 200px;
  margin-left: 40px;
}

._spacer>p
{
  font-size: var(--normal-text);
  font-weight: normal;
}
._spacer>p>span{color: var(--blue);}
._spacer>p>i{color: var(--orange); font-style: normal;}
._spacer>._qrcode>._qrcode_svg path {fill: white;}
._spacer>._qrcode>p
{
  color: var(--blue);
  /* margin-top: 5px; */
  font-size: 1em;
  padding: 4px 0;
}
._spacer>._qrcode>p>svg{width: 1.2em;height: 1.2em;margin-bottom: -4px;}

._grade_date>h3
{
  color: var(--orange);
  font-weight: normal;
  font-size: 1.2em;
}
._grade_date>h4
{
  color: var(--blue);
  font-weight: normal;
  font-size: 1em;
}
._grade_loc>h2
{
  font-size: 1.6em;
  font-weight: 600;
}
._grade_loc>p
{
  font-size: 1em;
}

._courses
{
  padding: 0 10px;
}
._courses>p{font-size: 1.1em; padding: 0 10px;}
._courses>p>span{color: var(--blue);}

@media (max-width: 900px)
{
  ._flex_col.sm_flex_col
  {
    flex-direction: column;
  }
  ._flex_col.sm_flex_col>._courses
  {
    padding: 0;
    padding-top: 10px;
  }
  ._flex_col.sm_flex_col>._courses>p:nth-child(1){padding-left: 0;}
  ._flex_col.sm_flex_col>._courses>p:last-child{padding-right: 0;}
  ._spacer>._qrcode
  {
    float: none;
    margin: 0 auto;
    height: auto;
  }
  ._spacer>h1
  {
    font-size: 2.5em;
  }
}